header {
  height: 3rem;
  position: relative;
  background-color: $primary-brand;
  padding: 1em;
  color: $white;

  .logo {

    letter-spacing: 0.25rem;
    padding: 1em 0 0 5em;

    color: $white;
  }

  .logo.toggle {
    display: none;
  }

  .burger {
    display: none;
    background-color: transparent;
    border: 0;

    div {
      width: 1.75rem;
      height: 0.25rem;
      background-color: $white;
      margin: 0.25rem;
      transition: transform 0.3s ease-in;
    }
  }

  .toggle #line1 {
    transform: rotate(-45deg) translate(-5px, 6px);
  }

  .toggle #line2 {
    opacity: 0;
  }

  .toggle #line3 {
    transform: rotate(45deg) translate(-5px, -6px);
  }


  #inputSearch {
    padding: 0.25em;
    width: 30%;
  }

  .hideInput {
    visibility: hidden;
  }

  .btn {
    background-color: transparent;
    outline: none;
    border: none;
  }


  .nav-links {

    display: flex;

    li {
      list-style: none;
      padding: 0.75em;
    }

    a {
      text-decoration: none;
      @extend .regualr-body-m-16-;
      color: $white;
      ;
    }
  }

  .bottomLinks {
    display: none;
  }




  .headerOuter {
    @include flexJust;
    justify-content: space-between;

    width: 90vw;
  }

  .endHeader {
    @include flexAlignJust;


    label,
    a {
      @extend .regualr-body-m-16-;
    }

    img {
      padding: 0 0.37em;
    }

    .shoppingBagImg {
      position: relative;
    }

    .totalcartitems {

      margin: -20px 0 0 -10px;
      width: 15px;
      height: 15px;
      background-color: black;
      border-radius: 50%;
      padding: 0.125em;
      z-index: 2;
      @include flexAlignJust;


    }
  }
}